"use client";

import { useState, useCallback, useEffect } from "react";

interface AwsCredential {
  id: string;
  name: string;
  region: string;
  createdAt: string;
}

interface UseAwsCredentialsResult {
  credentials: AwsCredential[];
  selectedCredential: AwsCredential | null;
  loading: boolean;
  error: string | null;
  fetchCredentials: () => Promise<void>;
  selectCredential: (id: string) => void;
  addCredential: (
    name: string,
    accessKeyId: string,
    secretKey: string,
    region: string
  ) => Promise<boolean>;
}

export function useAwsCredentials(): UseAwsCredentialsResult {
  const [credentials, setCredentials] = useState<AwsCredential[]>([]);
  const [selectedCredential, setSelectedCredential] = useState<AwsCredential | null>(null);
  const [loading, setLoading] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);

  const fetchCredentials = useCallback(async () => {
    try {
      setLoading(true);
      setError(null);
      
      const response = await fetch("/api/aws/credentials");
      
      if (!response.ok) {
        const { error } = await response.json();
        throw new Error(error || "Erro ao buscar credenciais");
      }
      
      const data = await response.json();
      setCredentials(data);
      
      // Auto-selecionar a primeira credencial, se disponível
      if (data.length > 0 && !selectedCredential) {
        setSelectedCredential(data[0]);
      }
    } catch (err) {
      setError(err instanceof Error ? err.message : "Erro ao buscar credenciais");
    } finally {
      setLoading(false);
    }
  }, [selectedCredential]);

  const selectCredential = useCallback((id: string) => {
    const credential = credentials.find(cred => cred.id === id);
    if (credential) {
      setSelectedCredential(credential);
    }
  }, [credentials]);

  const addCredential = useCallback(async (
    name: string,
    accessKeyId: string,
    secretKey: string,
    region: string
  ) => {
    try {
      setLoading(true);
      setError(null);
      
      const response = await fetch("/api/aws/credentials", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          name,
          accessKeyId,
          secretKey,
          region,
        }),
      });
      
      if (!response.ok) {
        const { error } = await response.json();
        throw new Error(error || "Erro ao adicionar credencial");
      }
      
      // Atualizar lista de credenciais
      await fetchCredentials();
      return true;
    } catch (err) {
      setError(err instanceof Error ? err.message : "Erro ao adicionar credencial");
      return false;
    } finally {
      setLoading(false);
    }
  }, [fetchCredentials]);

  // Buscar credenciais ao montar o componente
  useEffect(() => {
    fetchCredentials();
  }, [fetchCredentials]);

  return {
    credentials,
    selectedCredential,
    loading,
    error,
    fetchCredentials,
    selectCredential,
    addCredential,
  };
} 