"use client";

import { useState, useEffect } from "react";
import { useAwsCredentials } from "@/hooks/useAwsCredentials";
import { motion } from "framer-motion";

interface S3Bucket {
  name: string;
  creationDate: string;
}

export default function S3Page() {
  const { selectedCredential } = useAwsCredentials();
  const [buckets, setBuckets] = useState<S3Bucket[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchBuckets() {
      if (!selectedCredential) {
        setLoading(false);
        return;
      }

      try {
        setLoading(true);
        setError(null);
        
        console.log(`Buscando buckets S3 para credencial: ${selectedCredential.id}`);
        const response = await fetch(`/api/aws/resources?credentialId=${selectedCredential.id}&type=S3`);
        
        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.error || "Erro ao buscar buckets S3");
        }
        
        const data = await response.json();
        console.log("Resposta da API:", data);
        setBuckets(data.resources || []);
      } catch (err) {
        console.error("Erro ao buscar buckets:", err);
        setError(err instanceof Error ? err.message : "Erro ao buscar buckets");
      } finally {
        setLoading(false);
      }
    }

    fetchBuckets();
  }, [selectedCredential]);

  return (
    <div>
      <motion.div
        className="mb-6"
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.3 }}
      >
        <h1 className="text-3xl font-bold text-gray-900">Buckets S3</h1>
        <p className="mt-2 text-gray-600">
          Visualize e monitore seus buckets de armazenamento S3 na AWS.
        </p>
      </motion.div>

      <motion.div
        className="bg-white rounded-lg shadow-md p-6"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.3 }}
      >
        {!selectedCredential ? (
          <div className="text-center py-6">
            <p className="text-gray-500">Selecione uma credencial AWS para visualizar os buckets.</p>
          </div>
        ) : loading ? (
          <div className="flex justify-center py-8">
            <div className="animate-spin rounded-full h-8 w-8 border-t-2 border-b-2 border-indigo-500"></div>
          </div>
        ) : error ? (
          <div className="p-4 bg-red-50 rounded-md">
            <p className="text-red-700">{error}</p>
          </div>
        ) : buckets.length === 0 ? (
          <div className="text-center py-6">
            <p className="text-gray-500">Nenhum bucket S3 encontrado.</p>
            <p className="text-sm text-gray-400 mt-2">
              Os buckets S3 são globais na AWS, mas podem ser limitados pela permissão da credencial.
            </p>
          </div>
        ) : (
          <div>
            <div className="flex justify-between items-center mb-4">
              <h2 className="text-lg font-medium text-gray-900">
                {buckets.length} {buckets.length === 1 ? "Bucket" : "Buckets"} Encontrados
              </h2>
              <span className="text-sm text-gray-500">
                Região da Credencial: {selectedCredential.region}
              </span>
            </div>
            
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {buckets.map((bucket) => (
                <div key={bucket.name} className="border rounded-lg p-4 hover:bg-gray-50 transition-colors">
                  <div className="flex items-start space-x-3">
                    <div className="flex-shrink-0">
                      <svg className="h-8 w-8 text-indigo-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 8h14M5 8a2 2 0 110-4h14a2 2 0 110 4M5 8v10a2 2 0 002 2h10a2 2 0 002-2V8m-9 4h4" />
                      </svg>
                    </div>
                    <div>
                      <h3 className="text-md font-medium text-gray-900">{bucket.name}</h3>
                      <p className="mt-1 text-sm text-gray-500">
                        Criado em: {new Date(bucket.creationDate).toLocaleDateString()}
                      </p>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}
      </motion.div>
    </div>
  );
} 